import React from 'react';
import Article from './article.js'
import './nav.css';

class Menu extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      search: '',
      open: false,
      checked: {}
    }
    this.handleSearch = this.handleSearch.bind(this);
    this.bulk = this.bulk.bind(this);
    this.check = this.check.bind(this);
  }
  // ** Search bar **
  searchbar() {
    let targets = this.props.targets;
    if (this.state.open) {
      return (
        <div className='bar'>
          <input className='search' type="text" value={this.state.search} onChange={this.handleSearch} placeholder="Search..." autoFocus></input>
          <select id={this.props.group + '-filter'} onChange={this.bulk}>
            <option>- Select New Group -</option>
            {targets.map(t => (
              <option>{t}</option>
            ))}
          </select>
        </div>
      )
    }
  }
  handleSearch(event) {
    var query = event.target.value;
    this.setState({search: query});
  }
  searchList() {
    let list = this.props.data;
    var results = [];
    for (var l in list) {
      if (list[l].toUpperCase().includes(this.state.search.toUpperCase())) {
        results.push(list[l])
      }
    }
    return results;
  }
  // ** Fill results **
  fill() {
    var results = this.searchList();
    if (this.state.open) {
      return (
        <ul>
          {results.map((article,index) => (
            <Article key={index} title={article} preview={() => this.preview(article)} targets={this.props.targets} group={this.props.group} update={this.props.update} checked={this.checked(article)} check={this.check} />
          ))}
        </ul>
      )
    }
  }
  expand() {
    if (this.state.open) {
      this.setState({open:false})
    } else (
      this.setState({open:true})
    )
  }
  preview(title) {
    // var url = 'https://www.papercut.com/kb/' + this.props.group + '/' + title;
    let changes = this.props.changes;
    var list = Object.values(changes);
    var keys = Object.keys(changes);
    var url;
    if (list.includes(this.props.group + '.' + title)) {
      var target = keys.find(key => changes[key] === this.props.group + '.' + title)
      var group = target.split('.');
      url = 'https://www.papercut.com/kb/' + group[0] + '/' + title;
    } else {
      url = 'https://www.papercut.com/kb/' + this.props.group + '/' + title;
    }
    document.getElementById('preview').setAttribute('src', url);
  }
  bulk(e) {
    let checks = this.state.checked;
    let list = [];
    for (var c in checks) {
      if (checks[c]) {
        list.push(c);
        checks[c] = false;
      }
    }
    if (list.length === 0) {
      list = this.searchList();
    }
    if (e.target.value !== '- Select New Group -') {
      for (var l in list) {
        this.props.update(e.target.value, list[l], this.props.group)
      }
    }
    document.getElementById(this.props.group + '-filter').selectedIndex = '0';
    this.setState({checked: checks});
  }
  check(e) {
    var article = e.target.name;
    let checks = this.state.checked;
    if (!checks[article]) {
      checks[article] = false;
    }
    if (this.state.checked[article]) {
      checks[article] = false;
    } else {
      checks[article] = true;
    }
    this.setState({checked: checks});
  }
  checked(item) {
    let checks = this.state.checked;
    if (checks[item]) {
      return checks[item];
    } else {
      return false;
    }
  }
  render() {
    return (
      <div className='group' id={this.props.group}>
        <header onClick={() => this.expand()}>
          {this.props.group}<span><strong>{this.props.data.length}</strong></span>
        </header>
        {this.searchbar()}
        <div className='content'>
          {this.fill()}
        </div>
      </div>
    )
  }
}

export default Menu;
