import React from 'react';

class Article extends React.Component {
  constructor(props) {
    super(props);
    this.update = this.update.bind(this);
  }
  assign(article) {
    let targets = this.props.targets;
    return (
      <select id={article + '-filter'} name={article} onChange={this.update}>
        <option>- Select New Group -</option>
        {targets.map((t, index) => (
          <option key={index}>{t}</option>
        ))}
      </select>
    )
  }
  update(e) {
    if (e.target.value !== '- Select New Group -') {
      this.props.update(e.target.value, e.target.name, this.props.group);
      document.getElementById(this.props.title + '-filter').selectedIndex = 0;
    }
  }
  render() {
    return (
      <li><input type='checkbox' name={this.props.title} checked={this.props.checked} onChange={this.props.check}></input><p onClick={this.props.preview}>{this.props.title}</p>{this.assign(this.props.title)}</li>
    )
  }
}

export default Article;
