import React from 'react';
import Menu from './components/nav.js';
import DB from './db.json';
import './app.css';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      raw: DB,
      filtered: {},
      changelog:{},
      editing: false
    }
    this.createGroup = this.createGroup.bind(this);
    this.updateRecord = this.updateRecord.bind(this);
  }
  log(x,y,z) {
    let changes = this.state.changelog;
    var changekey = z + '.' + y;
    var changevalue = x + '.' + y;
    changes[changekey] = changevalue;
    return(changes)
  }
  updateRecord(a, b, c) {
    var log = this.log(a,b,c);
    let newGroups = this.state.filtered;
    newGroups[a].push(b);
    var oldRaw;
    if (this.state.raw[c]) {
      oldRaw = this.state.raw
      var rawIndex = oldRaw[c].indexOf(b);
      oldRaw[c].splice(rawIndex, 1);
      this.setState({
        filtered:newGroups,
        raw:oldRaw,
        changelog:log
      });
    } else {
      oldRaw = this.state.filtered
      var filteredIndex = oldRaw[c].indexOf(b);
      oldRaw[c].splice(filteredIndex, 1);
      this.setState({filtered:newGroups,changelog:log});
    }
  }
  createGroup(e) {
    if (e.key === 'Enter') {
      let dummy = this.state.filtered;
      dummy[e.target.value] = [];
      this.setState({
        filtered: dummy,
        editing: false
      })
    }
  }
  edit() {
    if (this.state.editing) {
      return (
        <input id="edit" type="text" autoFocus onKeyPress={this.createGroup}></input>
      )
    } else {
      return (
        'Add New Group'
      )
    }
  }
  download() {
    var dataStr = "data:text/json;charset=utf-8," + encodeURIComponent(JSON.stringify(this.state.changelog));
    var downloadAnchorNode = document.createElement('a');
    downloadAnchorNode.setAttribute("href",     dataStr);
    downloadAnchorNode.setAttribute("download", 'changes.json');
    document.body.appendChild(downloadAnchorNode); // required for firefox
    downloadAnchorNode.click();
    downloadAnchorNode.remove();
  }
  render() {
    var groups = [];
    for (const section in this.state.raw) {
      groups.push(section);
    }
    var f_groups = [];
    for (const x in this.state.filtered) {
      f_groups.push(x);
    }
    return (
        <div id='main'>
          <nav id='current'>
            <h2>Current Groups</h2>
            {groups.map((group, index) => (
              <Menu key={index} data={this.state.raw[group]} group={group} targets={f_groups} update={this.updateRecord} changes={this.state.changelog} />
            ))}
          </nav>
          <nav id='future'>
            <h2>New Groups</h2>
            {f_groups.map((fg, index) => (
              <Menu key={index} data={this.state.filtered[fg]} group={fg} new={true} targets={f_groups} update={this.updateRecord} changes={this.state.changelog} />
            ))}
            <div className='group'>
              <header id='new-group' onClick={() => this.setState({editing: true})}><i className='fas fa-plus'></i>{this.edit()}</header>
            </div>
            <div id='download'>
              <button onClick={() => this.download()}>Download</button>
            </div>
          </nav>
          <iframe title='Previewer'id="preview">
          </iframe>
        </div>
    );
  }
}

export default App;
